/**
 - Here is where you can add styles for global elements
 */

/*--------------------------------------------------------------
# Headers
--------------------------------------------------------------*/
.entry-header, .page-header {
  position: relative;
  min-height: 22vmax;
  padding: 30px 0;
  color: white;
  background: $bg-header;

  h1 {
    margin: 0;
  }

  .cover-image {
    height: 500px;
  }

}


/*------- Items directly under header should have padding --------*/
.container-content > *:first-child {
  padding-top: $gap;
}

/*------- When header has no min-height and text isnt on top of any image --------*/
.header-basic {
  min-height: 0;
  height: auto;
  color: black;
  padding: 0;
  margin-bottom: $gap;

  .cover-image {
    margin-bottom: $gap; //above title
  }
}

/*--------------------------------------------------------------
# Global content
--------------------------------------------------------------*/

.edit-link {
  @include font-size(.7);
}

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.footer-grid {
  justify-items: center;
  margin-bottom: $gutters;

  div:first-child {
    justify-self: start;
  }

  div:last-child {
    justify-self: end;
  }
}

.site-footer {
  padding-top: $gap;
  padding-bottom: 5px;
  background: $black;
  color: $grey;
  @include font-size(.7);
}

/*--------------------------------------------------------------
# Error 404 Page
--------------------------------------------------------------*/
.error404 main {
  min-height: 80vh;

  .container-content {
    background: rgba(black, .6);
    color: white;
  }
}

/*--------------------------------------------------------------
# Animations
--------------------------------------------------------------*/

[data-scrollanimation], .animated, [data-toggle]{
  animation-fill-mode: both;
  animation-duration: 1s;
  transform: translate(0);
}

//chose to go with animation instead of transform, because transform requires a class for starting position and a class for ending.
//animation can use one class with keyframes for starting and ending.
@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

.fixed-at-top {
  position: fixed;
  width: 100%;
  top: 0;
}

/*--------------------------------------------------------------
# Scrub animations. Expect to go from a start class to an end class
--------------------------------------------------------------*/
//add start class as regular class. Then use scrollanimation for end class.
.js .animateIn {
  opacity: 0; //start at nothing
}
