//when the right panel should overlay instead of pushing over the site (become absolute)
$panel-right--breakpoint: 1030px;

//sidebar width
$sidebar-width: 350px;

//when the nav goes mobile.
//needed for when to add and remove padding and room for sidebar button
$nav-breakpoint: $nav-breakpoint-collapse;

//Styling can be found at the bottom of this file
/*--------------------------------------------------------------
# Panel Right button
--------------------------------------------------------------*/

/*------- toggle button --------*/
.panel-right-toggle {
  z-index: 92;
  position: absolute;
  right: 0;
  top: 0;
  line-height: 0;
  border: none;
  padding: 0 15px;
  cursor: pointer;
  outline: none;
  font-size: 22px;

  &:focus {
    outline: none;
  }
}

/*------- default if no icon is found with svg --------*/
.sidebar-icon {
  border: 2px solid white;
  border-radius: 1px;
  position: relative;
  height: 1.3rem; //may have to play to get it matching font size
  width: 22px;
  display: block;

  &:after {
    content: '';
    width: 30%;
    background: white;
    height: 100%;
    position: absolute;
    right: 0;
  }
}

//hide button if sidebar empty
#panel-right:empty ~ .panel-right-toggle {
  display: none;
}

//hide secondary if empty
#secondary:empty {
  display: none;
}

//making sure the menu doesn't bump into the sidebar button when window is somewhat narrow
//if the screen is smaller than container plus 30 and bigger then mobile give some padding to the side of nav container and make room for sidebar icon
@media (max-width: $container + $gap) and (min-width: $nav-breakpoint) {
  #panel-right:not(:empty) ~ .site .site-top .container {
    padding-right: 52px;
  }
}

//if site nav is full width, add room for sidebar button always
#panel-right:not(:empty) ~ .site .site-top .container-fluid {
  padding-right: 52px;
}

//open panel 
#panel-right.open:not(:empty) {
  flex-basis: $sidebar-width;
  flex-shrink: 0;
}

/*--------------------------------------------------------------
# Panel Breakpoint to become absolute
--------------------------------------------------------------*/
@media(max-width: $panel-right--breakpoint) {
  #panel-right {
    position: absolute;
    height: 100%;
    right: 0;
    top: 0;
    padding-top: 50px;
    z-index: 90;
    width: 0;

    &.open {
      width: $sidebar-width;
    }
  }
}

/*--------------------------------------------------------------
# When sidebar is in panel right
--------------------------------------------------------------*/
#panel-right {
  .sidebar-holder {
    padding-top: $gutters;
    height: 100%;
    overflow: auto;
    overflow-x: hidden;
    min-width: $sidebar-width;
    -webkit-overflow-scrolling: touch;
  }
  @extend .hide-scroll;
}

/*--------------------------------------------------------------
# Sidebar Page Grid Template Work
--------------------------------------------------------------*/
.sidebar-left #secondary {
  order: 0;
}

.sidebar-left #primary {
  order: 1;
}

//already has containers paddings
.sidebar-template .entry-content {
  padding: 0;
}

//loads full inside container. gets pulled out with javascript very fast but not fast enough to see edges...
.sidebar-template header {
  margin-left: - #{$gutters};
  margin-right: - #{$gutters};
}

#secondary {
  margin-left: 0;
}

.js .sidebar-template:not(.active) {
  #secondary {
    display: none; //hides page until shown via js after header has been moved out of the article and put above and wide
  }

  max-width: 100%;
}

.sidebar-holder {
  position: relative;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  li {
    position: relative;
    margin: 0;
    color: inherit;
    transition: all .5s;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    a {
      text-decoration: none;
      outline: none;
      padding: 5px 0;
      color: inherit;
      cursor: pointer;
    }
  }
}

.sidebar-holder .widget {
  padding: $gap $gutters;
}

/*--------------------------------------------------------------
# Panel and Sidebar Styling
--------------------------------------------------------------*/

//sidebar button if active
.panel-right-toggle {
  color: white;
  background: transparent;
}

.panel-right-toggle.toggled-on {
  color: white;
}

/*-------  #panel Right --------*/
#panel-right {
  background: rgb(30, 30, 34);
  color: white;
  h2 {
    text-transform: capitalize;
    color: white;
  }
}

/*------- Sidebar Background --------*/
#secondary .sidebar-holder:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 50vw;
  top: 0;
  left: 0;
  background: #efefef;
  z-index: -1;
}

//background when on left
.sidebar-left #secondary .sidebar-holder:after {
  left: auto;
  right: 0;
}


