/*--------------------------------------------------------------
# Featured and Header Images
--------------------------------------------------------------*/
/*
    - Use a background image div for holding a foreground image behind an item or header
    - use cover-image for an image in the foreground of a header. This has a height you can change
*/
.background-image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: block;

  img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    position: relative;
  }

  ~ *{
    z-index: 1;
  }
}


.cover-image {
  width: 100%;
  display: block;
  img {
    object-fit: cover;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
  }
}

//for fallbacks
.cover-image, .background-image {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.no-objectfit .background-image img,
.no-objectfit .cover-image img {
  display: none;
}


/*--------------------------------------------------------------
# Article images
--------------------------------------------------------------*/
article img {
  display: block;
}

figure {
  margin-bottom: 1em;
}

.wp-block-images figcaption {
}

/*--------------------------------------------------------------
# Image Alignments
--------------------------------------------------------------*/
/*
  - Gutenberg does not make this easy
  - We float the img inside the figure that has .alignleft applied.
  - This makes the .alignleft element have the same main column width as text.
  - What makes the float work is that the figure collapses into a zero height element
 */
figure {
  margin: 0;
}

.alignleft img {
  display: block;
  float: left;
  width: 50%;
  margin-right: 2em;
}

.alignright img {
  display: block;
  float: right;
  width: 50%;
  margin-left: 2em;
}

.alignright figcaption,
.alignleft figcaption {
  color: $color-soft;
  width: 50%;
  margin-bottom: 1em;
}

.alignleft figcaption {
  float: left;
  clear: left;
  margin-right: 2em;
}

.alignright figcaption {
  float: right;
  clear: right;
  margin-left: 2em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.full-width, .alignfull, .alignwide {
  & > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

/*--------------------------------------------------------------
# Videos
--------------------------------------------------------------*/
.videowrapper {
  float: none;
  clear: both;
  width: 100%;
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 0;
  height: 0;
}

.videowrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
