/*--------------------------------------------------------------
# Main Navigation on site
--------------------------------------------------------------*/

/*
    - For styling search for the word "Styling"
    - you can style site-top background right below in site-top
*/

/*------- Vars --------*/
//when the mobile nav should kick in. Change depending on how many items you have! Make sure to make this match the navBreakpoint in navigation.custom.js
$nav-breakpoint-collapse: 800px;
$nav_gutter: 15px; //space between nav items
$desktop-menu-alignment: center; //can be flex-end or flex-start or center for menu-items positioning
$logo-width: 350px;

/*------- Site-top --------*/
.site-top {
  z-index: 90;
  width: 100%;
}

/*------- Navigation --------*/
.site-navigation {
  flex-wrap: wrap;
  position: relative;
  align-items: center;
  justify-content: center;
  margin: auto;
  height: 100%;

  .site-logo {
    max-width: $logo-width;
    margin-bottom: 0;
    a + a {
      display: none;
    }
    a {
      transition: all .5s;
    }
  }
}

/*------- Site-logo --------*/
.site-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  @include font-size(2);
  text-transform: capitalize;
}

.site-logo h1,
.site-logo p {
  margin: 0;
}

.site-logo__link, .site-title a, .custom-logo-link {
  display: flex;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  font-weight: normal;
  outline: none;
  padding: 5px 0;
  text-decoration: none;

  img {
    display: block;
  }
}

//logo positions
@media (min-width: $nav-breakpoint-collapse) {

  //layout positions
  .logo-left .site-logo {
    justify-content: left;
  }

  .logo-right .site-logo, .logo-center-under .site-logo {
    order: 2;
  }

  .logo-center .site-navigation, .logo-center-under .site-navigation {
    flex-direction: column;
    align-items: center;

  }

  .no-logo .site-logo {
    display: none;
  }

  .logo-in-middle > .site-logo, .logo-in-middle > .container .logo-in-middle {
    display: none;
  }

  .logo-in-middle .site-navigation__nav-holder {
    justify-content: center;
  }

  .logo-right .site-navigation__nav-holder {
    justify-content: flex-start;
  }

}

/*------- sub-menu arrow button toggle --------*/
.submenu-dropdown-toggle .icon {
  transition: all .4s;
}

.submenu-dropdown-toggle,
.submenu-dropdown-toggle:focus {
  background: none;
  border: none;
  padding: 5px $nav_gutter 5px 0;
  margin: 0;
  display: inline;
  outline: none;
  cursor: pointer;
  min-width: 1.5em;
  text-align: left;
  font-size: inherit;
  color: inherit;

  &.toggled-on .icon {
    transform: rotate(90deg);
  }
}

.site-navigation__nav-holder {
  @extend .hide-scroll;
}

/*------- Navigation holder on mobile --------*/

@media(max-width: $nav-breakpoint-collapse) {

  .site-navigation__nav-holder {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    position: absolute;
    top: 0;
    height: 100vh;
    left: 0;
    padding: 60px 0 0 0;
    z-index: 80;
    width: 50vw;
    transform: translate(-100vw, 0);
    will-change: transform;

    &.toggled-on {
      transform: translate(0, 0);
    }
  }

  .site-top.toggled-on {
    top: 0;
    width: 100%;
  }
  .site-navigation__nav {
    overflow: auto; //allow scrolling the menu
    height: 90%;
  }
}

//only transition AFTER when resizing browser
@media (max-width: $nav-breakpoint-collapse - 30px) {
  .site-navigation__nav-holder {
    transition: all .5s;
    margin: 0;
  }
}

/*------- Menus --------*/

.menu,
.menu > ul {
  //when there is a menu its ul.menu and when its defaulting to pages its .menu ul...annoying
  padding: 0;
  list-style: none;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  li {
    position: relative;
    margin: 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap; //needed to put submenus on next line

    a {
      text-decoration: none;
      outline: none;
      padding: 5px $nav_gutter;
      color: inherit;
      cursor: pointer;
      flex: 1 0 auto;
      transition: all .5s;
    }
  }

}

/*------- sub-menus --------*/

ul.sub-menu, ul.children {
  list-style: none;
  margin: 0;
  display: none;
  transition: none;
  overflow: hidden;
  width: 100%;
  padding: 0;

  //inner items move over a bit
  a {
    padding-left: 1.5em;
  }

}

/*------- Desktop Menu changes --------*/

@media (min-width: $nav-breakpoint-collapse) {

  .site-navigation {
    flex-wrap: nowrap;
    align-items: stretch;
  }

  .site-navigation__nav, .site-navigation__nav > div,
  .menu,
  .menu > ul {
    height: 100%;

    li {
      align-items: $desktop-menu-alignment;
    }
  }

  .site-navigation__nav-holder {
    background: transparent;
    flex-direction: row;
    justify-content: flex-end; //where the stuff should appear. near logo or other side
    align-items: center;
    position: static;
    padding: 0;
    height: auto;
    width: auto;
    display: flex;
    transform: translate(0, 0);
  }

  .site-navigation .menu,
  .site-navigation .menu > ul,
  .horizontal-menu {
    align-items: stretch;
    flex-direction: row;

    li a {
      padding: 3px 20px; //padding on desktop top menu items
      max-width: none;
    }

    .submenu-dropdown-toggle,
    .submenu-dropdown-toggle:focus {
      padding: 3px 10px 3px 0;
    }

    .menu-item-has-children > a, .page_item_has_children > a {
      padding-right: 2px; //because there's an arrow icon next to it now
    }
  }

  .site-navigation {

    .menu .sub-menu, .menu .children {
      display: block;
      overflow: visible;
      opacity: 0;
      visibility: hidden;
      transition: all .5s;
      transition-delay: .2s;
      margin: 0;
      position: absolute;
      z-index: 10;
      top: 100%;
      left: 50%;
      padding: 5px 0;
      width: 150px;
      margin-left: -75px; //half of width, to center the submenu
      border-radius: 0 0 3px 3px;

      li > *, li > *:focus { //children items
        padding: 2px 5px;
        transition: all .5s;
      }

      //centered pointy piece above submenu
      &:before {
        //content: '';
        position: absolute;
        top: -6px;
        left: 0;
        right: 0;
        margin: auto;
        width: 12px;
        height: 12px;
        transform: rotate(45deg);
        border-radius: 4px 0 0 0;
        background: inherit;
      }
    }

    //sub-menu opens on hover or tabbed into focus or toggled open
    //.menu li:hover > ul,
    .menu li.focus > ul,
    .menu li.toggled-on > ul {
      opacity: 1;
      visibility: visible;
      //top: calc(100% + 5px);
      transition-delay: 0s;
    }

    //sub-menus of sub-menus. it can get really wild!
    li .page_item_has_children .children, li .sub-menu .sub-menu {
      display: none;
      position: relative;
      padding: 0;
      transition: none;
      &:before {
        content: none;
      }
    }

  }
  //end .site-navigation

}

//end desktop menu

/*------- Cool App like Menu --------*/

//page holder is wrapped around all content except the nav menu holder
.page-holder {
  z-index: 90;
  transition: transform .5s;
  will-change: transform;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: white;
}

.mobile-menu-open .page-holder {
  //when page opens
  transform: translate(50vw, -2vh) scale(.75);
  box-shadow: -3px 0 10px rgba(0, 0, 0, .5);
  border-radius: 4px;
}

@media (max-width: $nav-breakpoint-collapse) {
  .app-menu .site-navigation__nav-holder {
    position: absolute;
    z-index: 80;
    width: 100vw;
    display: none;
    transform: translate3d(0, 0, 0);
    padding-top: 10vh;

    &.open {
      display: block
    }

    .submenu-dropdown-toggle {
      flex: 1;
      min-width: 50vw;
    }
  }
}

//lock body so the user can scroll when menus are open or anything really... like a module opening
.menu-lock {
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  position: relative; //todo check on ios that it works
  touch-action: manipulate;
}

/*------- menu-toggle button --------*/

.site-navigation__menu-toggle {
  position: absolute;
  border: none;
  box-shadow: none;
  z-index: 99;
  cursor: pointer;
  will-change: transform;
  line-height: 0;
  height: 100%;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0 $nav_gutter;
  outline: none;
  transition: all .2s;
  background: transparent;

  &:focus {
    outline: none;
  }
}

//x version
.navigation-menu-icon {
  position: relative;
  display: block;
  height: 25px;
  width: 25px;
  border-radius: .5px;

  &:before,
  &:after {
    content: '';
    position: absolute;
    left: 0;
    top: 30%;
    height: 3px;
    width: 100%;
    background: white;
    transition: all .5s;
    outline: none;
    border-radius: inherit;
  }

  &:after {
    bottom: 30%;
    top: auto;
  }
}

//opened-state for x version
.site-navigation__menu-toggle.toggled-on .navigation-menu-icon:not(.navigation-menu-icon--alt) {
  &:before {
    transform: rotate(45deg);
    top: 47%;
  }
  &:after {
    transform: rotate(-45deg);
    bottom: 44%;
  }
}

//menu icon alt version
.navigation-menu-icon--alt {
  border-top: 3px solid white;
  margin-top: 6px;

  &:before,
  &:after {
    height: 3px;
  }

  &:before {
    top: 3px;
    width: 75%;
  }
  &:after {
    top: 9px;
  }
}

//hidden on desktop
@media (min-width: $nav-breakpoint-collapse) {
  .site-navigation__menu-toggle {
    display: none;
  }
}

/*--------------------------------------------------------------
# Styling
--------------------------------------------------------------*/

.site-top {
  background: linear-gradient(90deg, #FF5115 0%, #FFA72C 100%); //Styling for site top
}

body .navigation-menu-icon {
  border-color: white;
  color: white;

  &:after, &:before {
    background: white;
  }
}

/*------- site-logo --------*/
.site-logo__link, .site-title a, .custom-logo-link {
  color: white;
}

/*------- Mobile Styling --------*/
@media (max-width: $nav-breakpoint-collapse) {

  .site-navigation__nav-holder {
    background-image: linear-gradient(90deg, #FF5115 0%, #FEBC4A 100%);
  }

  .menu,
  .menu > ul {
    li {
      color: white;
    }
  }

  //current item
  li.focus >, li.current-menu-item >, li.current_page_item > {
    a, button, button:focus {
      background: rgba(black, .5);
    }

    //sub-menu opened
    .sub-menu {
    }
  }

}

/*------- Desktop Styling --------*/
@media (min-width: $nav-breakpoint-collapse) {
  .site-navigation {
    //all menu items
    li, li a, li button {
      background: transparent;
      color: white;
    }

    //top level menu items
    .menu > ul > li, ul.menu > li {
      @include font-size(.85);
      //current top level and focused top level
      &.focus,
      &:hover,
      &.toggled-on,
      &.current-menu-item,
      &.current-menu-ancestor {
        > a, > button {
          color: black;
          background: transparent;
        }
      }
    }

    /*------- submenu items --------*/
    //sub-menu block
    .sub-menu, .children {
      background: #FFA72C;
    }

    //sub-menu items
    .menu .sub-menu li, .menu .children li {

      &.focus,
      &:hover,
      &.toggled-on,
      &.current-menu-item,
      &.current-menu-ancestor {
        > a, > button {
          color: white;
          background: rgba(black, .5);
        }
      }
    }
  }
  //end site-navigation for desktop styling
}