/*--------------------------------------------------------------
# Site Wide Variables
--------------------------------------------------------------*/

/*------- Fonts --------*/
// - Change fonts as needed. you may of course need to add a link to your fonts for them to work in functions.php
$font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
$font-alt: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
$line-height: 1.5;
$font-pre: 'Source Code Pro', monospace;
$line-height-pre: 22px;


/*------- Grid and Layout --------*/
$container: 1200px; //the max size of site content
$content-container: 700px; //Size of actual article content
$gutters: 15px;
$gap: $gutters * 2;


/*------- Media Query Sizes --------*/
$media-small: 576px;
$media-medium: 767px;
$media-large: 1030px;
$media-container: $container;

$collapse-on-small: true; //collapse grid and flex-grid and flex on small devices automatically. small-span can override



//Colors

$pink: #EB6888;
$grey: #cacaca;
$dark-purple: #28303B;
$black: #191919;


//Applied Colors
$color-main: $black;
$color-link: $pink;
$color-highlight: $pink;
$bg-button: $pink;
$color-soft: $grey;
$color-input-border: $grey;
$bg-header: $dark-purple;
