/*--------------------------------------------------------------
# Layout Fixes for ie11
--------------------------------------------------------------*/

@media all and (-ms-high-contrast: none) {

  .layout-center-content > *{
      margin: 0; //ie11 freaks out if the inner div has a margin: auto;
    }


  .container .grid, .container-fluid .grid, .container-content .grid{
    margin-left: -#{$gutters};
    margin-right: -#{$gutters};
  }

  div .grid{
    display: flex;
    flex-wrap: wrap;

    & > * {
      margin-bottom: $gap;
      margin-right: $gutters;
      margin-left: $gutters;
    }

    //medium and below size on items
    @media(max-width: $media-medium) {
      @for $i from 1 through 12 {
        & > .medium-span-#{$i}, &.medium-span-all-#{$i} > * {
          $width: 100% / (12/$i);
          flex-grow: 1;
          flex-shrink: 1;
          flex-basis: calc(#{$width} - #{$gap});
        }
      }
    }

    //small and below size on items
    @media(max-width: $media-small) {
      @for $i from 1 through 12 {
        & > .small-span-#{$i}, &.small-span-all-#{$i} > * {
          $width: 100% / (12/$i);
          flex-grow: 1;
          flex-shrink: 1;
          flex-basis: calc(#{$width} - #{$gap});
        }
      }

      //no need to add class if set to collapse on small is always true. overrides any spans
      @if ($collapse-on-small == true) {
        flex-wrap: wrap;
        .site & > * {
          flex-basis: calc(100% - #{$gap});
        }
      }
    }

  }
}




/*--------------------------------------------------------------
# Sticky Footer "Fix"
--------------------------------------------------------------*/

@media all and (-ms-high-contrast:none){
  .site-content{
    min-height: 100vh;
  }
}

/*--------------------------------------------------------------
# Header fix
--------------------------------------------------------------*/

//ie11
@media all and (-ms-high-contrast: none) {
  .entry-header {
    height: 20vw;
  }

  .header-basic{
    height: auto;
  }
}


